
let MyButton = ({ children, clsName, active, ...props }) => {

   // –¥–ª—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ PizzaCategories. –ï—Å–ª–∏ –ø—Ä–æ–ø—Å –ø—Ä–∏—Ö–æ–¥–∏—Ç —Å –∫–ª–∞—Å—Å–æ–º _active, —Ç–æ –æ–Ω –≤–µ—à–∞–µ—Ç—Å—è –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç
   let activeClass = () => {
      if (active) {
         let newName = clsName + ' _active';
         return newName
      } else { return clsName }
   }

   return (
      <button className={activeClass()} {...props}> {children} </button>
   )
}

export default MyButton
