import React, { useState } from 'react';
import CartItemControl from './CartItemControl.jsx'

let CartItem = ({ cartItems, pizzaTotalHandler, pizzaTotal, cartItemsHandler }) => {

   // –∑–∞–¥–∞–µ—Ç –ø–æ—Ä—è–¥–∫–æ–≤—ã–π –Ω–æ–º–µ—Ä —Ç–æ–≤–∞—Ä–∞ –≤ –∫–æ—Ä–∑–∏–Ω–µ, —á—Ç–æ–±—ã –º–æ–∂–Ω–æ –±—ã–ª–æ –∫ –Ω–µ–º—É –æ–±—Ä–∞—Ç–∏—Ç—å—Å—è
   let fakeCartItems = [...cartItems]

   // –Ω—É–º–µ—Ä—É–µ—Ç –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å —Ç–æ–≤–∞—Ä–∞–º–∏ –≤ –∫–æ—Ä–∏–∑–Ω–µ, –¥–ª—è –¥–æ—Å—Ç—É–ø–∞ –∫ –Ω–∏–º
   fakeCartItems.forEach((elem, index) => {
      elem.numbPizzaCart = index
   })

   return (
      cartItems === null
         ? null
         :
         cartItems.map((elem, i) => {

            return (
               <div key={Math.random().toFixed(3)} className="main-cart__item cart-item">
                  <div className="cart-item__info">
                     <div className="cart-item__picture">
                        <img src={elem.img} className="cart-item__picture" alt={'pizza'} />
                     </div>
                     <div className="cart-item__text">
                        <p className="cart-item__name">{elem.name}</p>
                        <p className="cart-item__parameters">{elem.dough}, {elem.size}</p>
                     </div>
                  </div>

                  <CartItemControl
                     currentProduct={i}
                     fakeCartItems={fakeCartItems}
                     cartItemsHandler={cartItemsHandler}
                     pizzaTotalHandler={pizzaTotalHandler}
                     pizzaTotal={pizzaTotal}
                  />
               </div>
            )
         })
   )
}

export default CartItem


