import React from 'react';
import MyLi from './UI/MyLi/MyLi.jsx';

let PizzaItemControl = ({ pizzaParametr, pizzaChangeDough }) => {

   let changeDough = (e) => {
      // –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –≤ –∫–∞–∂–¥–æ–º –ø—É–Ω–∫—Ç–µ, –µ—Å–ª–∏ –æ–Ω —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –∏–∑ —Å—Ç–µ–π—Ç–∞, —Ç–æ –Ω–∞ –Ω–µ–≥–æ –≤–µ—à–∞–µ—Ç—Å—è –∫–ª–∞—Å—Å
      if (e === pizzaParametr[0].dough) {
         return '_active'
      }
   }

   let changeSize = (e) => {
      if (e === pizzaParametr[1].size) {
         return '_active'
      }
   }

   return (
      <div className="pizza-item__select-container">
         <ul className="pizza-item__type">
            <MyLi
               onClick={(e) => pizzaChangeDough(e)}
               className={`pizza-item__type ${changeDough('—Ç–æ–Ω–∫–æ–µ')} `}
            >—Ç–æ–Ω–∫–æ–µ</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e)}
               className={`pizza-item__type ${changeDough('—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ')} `}
            >—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ</MyLi>

         </ul>

         <ul className="pizza-item__size">
            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'low')}
               className={`pizza-item__size ${changeSize('25 —Å–º.')} `}
            >25 —Å–º.</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'average')}
               className={`pizza-item__size ${changeSize('30 —Å–º.')} `}
            >30 —Å–º.</MyLi>

            <MyLi
               onClick={(e) => pizzaChangeDough(e, 'high')}
               className={`pizza-item__size ${changeSize('35 —Å–º.')} `}
            >35 —Å–º.</MyLi>
         </ul>
      </div>
   )
}

export default PizzaItemControl