import React, { useState } from 'react';
import MyButton from './UI/MyButton/MyButton.jsx';

let PizzaCategories = ({ pizzaItems, sortedPizzaHandler }) => {
   let [activeType, activeTypeHundler] = useState({
      all: true,
      meat: false,
      cheese: false,
      vegan: false,
      sharp: false,
   })


   //–ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∫–Ω–æ–ø–∫—É –∫–∞—Ç–µ—Ä–æ—Ä–∏–π. –æ–±–Ω–æ–≤–ª—è–µ—Ç —Å—Ç–µ–π—Ç, —Å–±—Ä–∞—Å—ã–≤–∞—è —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–æ–∫, –∏ –ø—Ä–∏—Å–≤–∞–∏–≤–∞—è –Ω—É–∂–Ω–æ–π true
   let sortedType = (currentType) => {
      let pizzaActive = {
         all: false,
         meat: false,
         cheese: false,
         vegan: false,
         sharp: false,
      }
      pizzaActive[currentType] = true
      activeTypeHundler({ ...pizzaActive })

      // —Ñ–∏–ª—å—Ç—Ä—É–µ–º –ø–∏—Ü—Ü—ã –ø–æ –∫–æ–ª–±–µ–∫—É –∏–∑ –∫–Ω–æ–ø–∫–∏
      let mass = [...pizzaItems].filter((pizza) => {
         let namePizza = Object.keys(pizza)[0]
         if (pizza[namePizza].type.hasOwnProperty(currentType)) { return pizza }
      })
      sortedPizzaHandler([...mass])
   }

   return (
      <div className="main-pizza__categories">
         <MyButton
            onClick={(e) => sortedType('all')}
            clsName={"main-pizza__categories-button"}
            active={activeType.all}
         >
            –í—Å–µ
         </MyButton>

         <MyButton
            onClick={(e) => sortedType('meat')}
            clsName={"main-pizza__categories-button"}
            active={activeType.meat}
         >
            –ú—è—Å–Ω—ã–µ
         </MyButton>

         <MyButton
            onClick={(e) => sortedType('cheese')}
            clsName={"main-pizza__categories-button"}
            active={activeType.cheese}
         >
            –°—ã—Ä–Ω—ã–µ
         </MyButton>

         <MyButton
            onClick={(e) => sortedType('vegan')}
            clsName={"main-pizza__categories-button"}
            active={activeType.vegan}
         >
            –í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∏–µ
         </MyButton>

         <MyButton
            onClick={(e) => sortedType('sharp')}
            clsName={"main-pizza__categories-button"}
            active={activeType.sharp}
         >
            –û—Å—Ç—Ä—ã–µ
         </MyButton>

      </div>
   )
}

export default PizzaCategories

