import React, { useEffect, useState } from 'react';
import CartDelete from './CartDelete.jsx';

let CartItemControl = ({ fakeCartItems, currentProduct, pizzaTotal, pizzaTotalHandler, cartItemsHandler }) => {

   let priceProduct = fakeCartItems[currentProduct].price;
   //–æ–±—â–∏–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∑–∞–∫–∞–∑–∞ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–π –ø–∏—Ç—Ü—ã
   let [orderValues, setOrderValues] = useState({
      countTotal: 1,
      costTotal: priceProduct,
   })

   let changeQuantity = (elem) => {
      if (orderValues.countTotal >= 1 && orderValues.countTotal < 20 && elem === '+') {
         setOrderValues({
            ...orderValues,
            countTotal: orderValues.countTotal + 1,
            costTotal: orderValues.costTotal + priceProduct,
         })

         // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –æ–±—â–µ–µ –∫–æ–ª-–≤–æ –∑–∞–∫–∞–∑–∞ –≤ —Å—Ç–µ–π—Ç —Å –¥–∞–Ω–Ω—ã–º–∏
         fakeCartItems.forEach((elem, index, arr) => {
            if (index === currentProduct) {
               fakeCartItems[currentProduct].totalCount = orderValues.countTotal + 1
               fakeCartItems[currentProduct].totalPrice = orderValues.costTotal + priceProduct
            }
         })

         // –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∫–æ–ª-–≤–∞ –ø–∏—Ü—Ü –≤ –∑–∞–∫–∞–∑–µ, –≤—ã–∑—ã–≤–∞–µ—Ç —Å—Ç–µ–π—Ç –≥–¥–µ —Ö—Ä–∞–Ω–∏—Ç—Å—è –æ–±—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∑–∞–∫–∞–∑–µ
         pizzaTotalHandler({ ...pizzaTotal, quantity: pizzaTotal.quantity + 1, price: pizzaTotal.price + priceProduct })

      } else if (orderValues.countTotal > 1 && orderValues.countTotal < 20 && elem === '-') {
         setOrderValues({ ...orderValues, countTotal: orderValues.countTotal - 1 })

         fakeCartItems.forEach((elem, index, arr) => {
            if (index === currentProduct) {
               fakeCartItems[currentProduct].totalCount = orderValues.countTotal - 1
               fakeCartItems[currentProduct].totalPrice = orderValues.costTotal - priceProduct
            }
         })

         pizzaTotalHandler({ ...pizzaTotal, quantity: pizzaTotal.quantity - 1, price: pizzaTotal.price - priceProduct })
      }
   }

   // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –≤ —Å—á–µ—Ç—á–∏–∫–µ —Ç–µ–∫—É—â–µ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –∑–∞–∫–∞–∑–∞–Ω–Ω—ã—Ö –ø–∏—Ü—Ü, –∫–æ—Ç–æ—Ä—ã–µ —Ö—Ä–∞–Ω—è—Ç—Å—è –≤ —Å—Ç–µ–π—Ç–µ, –≤ –æ–±—ä–µ–∫—Ç–µ
   useState(() => {
      setOrderValues({
         ...orderValues,
         countTotal: fakeCartItems[currentProduct].totalCount,
         costTotal: fakeCartItems[currentProduct].totalPrice
      })
   }, [])

   return (
      <React.Fragment>
         <div className="cart-item__quantity">
            <div onClick={() => changeQuantity('-')} className="cart-item__quantity-minus"></div>
            <div className="cart-item__quantity-total">
               <p className="cart-item__quantity-counter">{orderValues.countTotal}</p>
               <input onChange={() => null} className="cart-item__quantity-input" value="1"></input>
            </div>
            <div onClick={() => changeQuantity('+')} className="cart-item__quantity-plus"></div>
         </div>
         <p className="cart-item__price">{orderValues.costTotal} <span>‚ÇΩ</span></p>
         
         <CartDelete
            cartItemsHandler={cartItemsHandler}
            fakeCartItems={fakeCartItems}
            currentProduct={currentProduct}
         />
      </React.Fragment>
   )
}

export default CartItemControl
