import React, { useState } from 'react';
import PizzaItemControl from './PizzaItemControl.jsx'

let PizzaItem = ({ pizza, cartItems, cartItemsHandler }) => {

   // —Å—Ç–µ–π—Ç —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏ –ø–∏—Ü—Ü—ã
   let [pizzaParametr, pizzaParametrHandler] = useState([
      { dough: '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ' },
      { size: '30 —Å–º.' },
      { price: pizza.price.average }
   ])

   // –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è –ø—Ä–æ–ø—Å–æ–º, –ø–æ–∑–≤–æ–ª—è–µ—Ç –∫–æ–ª–±–µ–∫–æ–º –ø–æ–ª—É—á–∞—Ç—å –¥–∞–Ω–Ω—ã–µ –∏–∑ –∫–ª–∏–∫–Ω—É—Ç–æ–≥–æ li, –∫–æ—Ç–æ—Ä—ã–µ –æ–∑–Ω–∞—á–∞—é—Ç
   // —Ä–∞–∑–º–µ—Ä –∏–ª–∏ —Ç–∏–ø —Ç–µ—Å—Ç–∞, –ø–æ—Å–ª–µ —á–µ–≥–æ –º–µ–Ω—è–µ—Ç—Å—è —Å—Ç–µ–π—Ç pizzaParametr –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç –≤—ã–±—Ä–∞–Ω–Ω—ã–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –≤ –ø—Ä–æ–ø—Å
   let pizzaChangeDough = (e, size) => {

      // –µ—Å–ª–∏ –∫–ª–∏–∫ –ø–æ —É–∂–µ –≤—ã–±—Ä–∞–Ω–Ω–æ–º—É –ø—É–Ω–∫—Ç—É, —Ç–æ return –∏ —Å—Ç–µ–π—Ç –Ω–µ –º–µ–Ω—è–µ—Ç—Å—è
      if (e.target.classList.contains('_active')) { return }

      // –µ—Å–ª–∏ –∫–ª–∏–∫ –ø–æ —Å–≤–æ–±–æ–¥–Ω–æ–º—É —ç–ª–µ–º–µ–Ω—Ç—É, —Ç–æ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è –∏–º—è –∫–ª–∞—Å—Å–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ —á–µ—Ä–µ–∑ –∫–æ–±–ª–µ–∫
      if (e.target.classList.contains('pizza-item__type')) {
         let mass = [...pizzaParametr];
         mass[0].dough = e.target.textContent.trim();
         pizzaParametrHandler([...mass]);

      } else {
         let mass = pizzaParametr;
         mass[1].size = e.target.textContent.trim();
         // –ø—Ä–∏ –≤—ã–±–æ—Ä–µ —Ä–∞–∑–º–µ—Ä–∞, –∫–æ–ª–±–µ–∫ –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ä–∞–∑–º–µ—Ä –ø–∏—Ü—Ü—ã,–∏–∑ –Ω–µ–≥–æ –º–æ–∂–Ω–æ —É–∑–Ω–∞—Ç—å —Ü–µ–Ω—É
         mass[2].price = pizza.price[size];
         pizzaParametrHandler([...mass]);
      }
   }

   let addCart = () => {
      let pizzaCartInfo = {
         dough: pizzaParametr[0].dough,
         size: pizzaParametr[1].size,
         price: pizzaParametr[2].price,
         img: './images/' + pizza.img,
         name: pizza.name,
         numbPizzaCart: null,
         totalCount: 1,
         totalPrice: pizzaParametr[2].price,
      }

      cartItemsHandler([...cartItems, { ...pizzaCartInfo }])
   }

   return (
      <div className="main-pizza__item pizza-item">
         <div className="pizza-item__picture">
            <img src={'images/' + pizza.img} alt={'Pizza'} />
         </div>
         <div className="pizza-item__content">
            <p className="pizza-item__title">{pizza.name}</p>
            <PizzaItemControl
               pizza={pizza}
               pizzaParametr={pizzaParametr}
               pizzaChangeDough={pizzaChangeDough}
            />
            <div className="pizza-item__bottom">
               <p className="pizza-item__price">{pizzaParametr[2].price} <span>‚ÇΩ</span></p>
               <button onClick={() => addCart()} className="pizza-item__button">–î–æ–±–∞–≤–∏—Ç—å</button>
            </div>
         </div>
      </div>
   )
}

export default PizzaItem