import React, { useState } from 'react';

let PizzaSort = ({ pizzaItems, sortedPizza, sortedPizzaHandler }) => {
   let [currentSelect, currentSelectHandler] = useState('1')

   let changeHandler = () => {
      // –æ—Ç–ª–∞–≤–ª–∏–≤–∞–µ—Ç –∏–∑–º–µ–Ω–µ–Ω–∏—è value –≤ select, –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç –≤ —Å—Ç–µ–π—Ç
      let elem = document.getElementById('select_id');
      if (currentSelect !== elem.value) {
         console.log('in select')

         currentSelectHandler(elem.value)

         // –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Å–µ–ª–µ–∫—Ç–∞, —Å–æ—Ä—Ç–∏—Ä—É–µ—Ç –ø–∏—Ü—Ü—ã
         if (elem.value === '1') {
            let fakePizza = [...pizzaItems]
            sortedPizzaHandler([...fakePizza])
         } else if (elem.value === '2') {
            let fakePizza = [...sortedPizza].sort((a, b) => {
               return b[Object.keys(b)[0]].price.low - a[Object.keys(a)[0]].price.low
            })
            sortedPizzaHandler([...fakePizza])
         } else if (elem.value === '3') {
            let fakePizza = [...sortedPizza].sort((a, b) => {
               return a[Object.keys(a)[0]].price.low - b[Object.keys(b)[0]].price.low
            })
            sortedPizzaHandler([...fakePizza])
         }

      }
   }

   return (
      <div className="main-pizza__sort">
         <span className="main-pizza__sort-title">–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</span>
         <div onClick={() => changeHandler()} className="main-pizza__sort-select">
            <select id={'select_id'} name="select_sort" className="turnintodropdown">
               <option value="1" >–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏</option>
               <option value="2" >—Ü–µ–Ω–µ (–º–∞–∫—Å)</option>
               <option value="3" >—Ü–µ–Ω–µ (–º–∏–Ω)</option>
            </select>
         </div>
      </div>
   )
}

export default PizzaSort